

interface TextColr {
  [prop: string]: string;
}

function getStyle(d: string): string {
  switch (d) {
    case '':
    case '':
    case '':
    case '':
    case '':
    case '':
      return 'bgc-r';
    case '':
    case '':
    case '':
    case '':  
    case '':
    case '':
      return 'bgc-b';
    case '':
    case '30':
    case '11':
      return 'bgc-g';
    default:
      if (/\d/.test(d)) return 'bgc-y';
      return '';
  }
}

const textColors: TextColr = {
  'bgc-r': 'text-r',
  'bgc-b': 'txt-b',
  'bgc-g': 'txt-g',
  'bgc-y': 'txt-y'
}

export function getStyleTxtColor(d: string): string {
  return textColors[getStyle(d)];
}

export default {
  getStyle,
  getStyleTxtColor
};
